.default-card-header {
  padding: 16px 10px;
  font-weight: 500;
  font-size: 18px;
}

.content-card__header--sticky-stuck .default-card-header {
  box-shadow: 1px 2px 10px rgba(34, 49, 59, 0.1);
}

@media (min-width: 768px) {
  .default-card-header {
    padding: 29px 20px 20px;
    font-size: 20px;
  }
}
